import React from 'react';

import CornerBorder from './CornerBorder';
import HorizontalBorder from './HorizontalBorder';
import VerticalBorder from './VerticalBorder';

/**
 * Top Left position of the window/dialog/popup
 */
export interface ScreenPosition {
  x: number;
  y: number;
}

/**
 * Width and height of the window/dialog/popup
 */
export interface FrameSize {
  width: number;
  height: number;
}

export interface FantasyBorderFrameProps {
  screenPosition: ScreenPosition;
  frameSize: FrameSize;
  children: React.ReactNode;
  primaryButton?: React.ReactElement;
  secondaryButton?: React.ReactElement;
  tileDimensions?: FrameSize;
  zIndex?: number;
  accessible?: boolean;
  flexibleSizing?: boolean;
}

// 50*180 since the base tile is vertical
export const defaultTileDimensions: FrameSize = {
  width: 50,
  height: 180,
};
const cornerSize = (tileDimensions: FrameSize): number =>
  Math.min(tileDimensions.width, tileDimensions.height);

const FantasyBorderFrame: React.FC<FantasyBorderFrameProps> = ({
  screenPosition,
  frameSize,
  children,
  primaryButton,
  secondaryButton,
  tileDimensions = defaultTileDimensions,
  zIndex = 1000,
  accessible = false,
  flexibleSizing = false,
}) => {
  const { x, y } = screenPosition;
  const { width, height } = frameSize;
  return (
    <>
      {/* Backdrop */}
      {!accessible && (
        <div
          style={{
            position: 'fixed',
            top: 0,
            left: 0,
            width: '100vw',
            height: '100vh',
            backgroundColor: 'rgba(0, 0, 0, 0.5)',
            zIndex: zIndex - 1,
            pointerEvents: 'none',
          }}
        />
      )}
      {/* Dialog */}
      <div
        style={{
          position: 'fixed',
          left: x,
          top: y,
          width,
          height,
          pointerEvents: 'auto',
          zIndex: zIndex,
        }}
      >
        {/* Corner ornaments */}
        <CornerBorder position="top-left" size={cornerSize(tileDimensions)} zIndex={zIndex + 1} />
        <CornerBorder position="top-right" size={cornerSize(tileDimensions)} zIndex={zIndex + 1} />
        <CornerBorder
          position="bottom-left"
          size={cornerSize(tileDimensions)}
          zIndex={zIndex + 1}
        />
        <CornerBorder
          position="bottom-right"
          size={cornerSize(tileDimensions)}
          zIndex={zIndex + 1}
        />

        {/* Horizontal border */}
        <HorizontalBorder
          side="top"
          tileDimensions={tileDimensions}
          length={width}
          zIndex={zIndex}
        />
        <HorizontalBorder
          side="bottom"
          tileDimensions={tileDimensions}
          length={width}
          zIndex={zIndex}
        />

        {/* Vertical border */}
        <VerticalBorder
          side="left"
          tileDimensions={tileDimensions}
          length={height}
          zIndex={zIndex}
        />
        <VerticalBorder
          side="right"
          tileDimensions={tileDimensions}
          length={height}
          zIndex={zIndex}
        />

        {/* Dialog content area */}
        <div
          style={{
            position: 'absolute',
            left: cornerSize(tileDimensions),
            top: cornerSize(tileDimensions),
            width: width - cornerSize(tileDimensions) * 2,
            height: flexibleSizing ? 'auto' : height - cornerSize(tileDimensions) * 2,
            maxHeight: flexibleSizing ? height - cornerSize(tileDimensions) * 2 : undefined,
            backgroundColor: !accessible ? 'rgba(0, 0, 0, 0.8)' : undefined,
            padding: !accessible ? '20px' : undefined,
            boxSizing: 'border-box',
            overflowY: flexibleSizing ? 'visible' : 'auto',
            zIndex: zIndex,
          }}
        >
          {children}
        </div>

        {/* Button area on bottom border */}
        {(primaryButton || secondaryButton) && (
          <div
            style={{
              position: 'absolute',
              left: cornerSize(tileDimensions),
              bottom: 0,
              width: width - cornerSize(tileDimensions) * 2,
              height: Math.min(Math.min(tileDimensions.height, tileDimensions.width), 60),
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              gap: '20px',
              zIndex: zIndex + 2,
            }}
          >
            {primaryButton && <div>{primaryButton}</div>}
            {secondaryButton && <div>{secondaryButton}</div>}
          </div>
        )}
      </div>
    </>
  );
};

export default FantasyBorderFrame;
